import React, { Component } from "react";
import { Tag } from "antd";
import MapPopover from "./MapPopover";

export default class HotelItemTitle extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }

  onLoad = () => {
    this.setState({ loading: false });
  };

  render() {
    const { loading } = this.state;
    let {
      hoteltranslatedname,
      address,
      chainname,
      brandname,
      latitude,
      longitude,
    } = this.props;

    if (chainname === "No chain") chainname = null;
    if (brandname === "No brand") brandname = null;

    return (
      <div>
        <h2>{hoteltranslatedname}</h2>
        {chainname ? <Tag color="blue">ËøûÈîÅ {chainname}</Tag> : null}
        {brandname ? <Tag color="geekblue">ÂìÅÁâå {brandname}</Tag> : null}
        <div>
          <sub>{address}</sub>
          <MapPopover
            loading={loading}
            onLoad={this.onLoad}
            latitude={latitude}
            longitude={longitude}
          ></MapPopover>
        </div>
      </div>
    );
  }
}
