import React, { Component } from "react";
import { Layout, Menu, message } from "antd";
import UserSetting from "./UserSetting";
import Reservations from "./UserReservations";
import { getReservations } from "../../services/reservationService";

const { Content, Sider } = Layout;

export default class User extends Component {
  state = {
    current: "reservations",
    loading: true,
  };

  componentDidMount() {
    getReservations()
      .then(({ data: reservations }) => {
        this.setState({ reservations, loading: false });
      })
      .catch(error => {
        message.error(error.message);
        this.setState({ loading: false });
      });
  }

  handleSelect = e => {
    this.setState({ current: e.key });
  };

  render() {
    const { reservations, current, loading } = this.state;

    return (
      <Layout className="site-layout-background admin-layout">
        <Sider className="site-layout-background" width={200}>
          <Menu
            mode="inline"
            defaultSelectedKeys={["reservations"]}
            onSelect={e => this.handleSelect(e)}
          >
            <Menu.Item key="reservations">ËÆ¢Âçï</Menu.Item>
            <Menu.Item key="profile">‰∏™‰∫∫</Menu.Item>
          </Menu>
        </Sider>
        <Content style={{ padding: "0 24px", height: "calc(100vh - 10em)" }}>
          {current === "reservations" ? (
            <Reservations
              loading={loading}
              reservations={reservations}
            ></Reservations>
          ) : (
            <UserSetting></UserSetting>
          )}
        </Content>
      </Layout>
    );
  }
}
