import auth from "./authService";

export function reserve(reservation) {
  const url = `${process.env.REACT_APP_SERVER}/reserves`;
  const body = new FormData();
  for (let key in reservation) body.append(key, reservation[key]);

  return fetch(url, {
    method: "POST",
    headers: {
      mode: "no-cors",
      Authorization: `Bearer ${auth.getToken()}`,
    },
    body,
  });
}

export function getReservations() {
  const url = `${process.env.REACT_APP_SERVER}/reserves`;

  // return new Promise(resolve => {
  //   resolve(reservations);
  // });

  return fetch(url, {
    method: "GET",
    headers: {
      Authorization: `Bearer ${auth.getToken()}`,
    },
  }).then(response => response.json());
}

export function getHotelReservations() {
  const url = `${process.env.REACT_APP_SERVER}/admins/reserves`;

  return fetch(url, {
    method: "GET",
    headers: {
      Authorization: `Bearer ${auth.getToken()}`,
    },
  });
}

export default {
  reserve,
};
