import React, { Component } from "react";
import queryString from "query-string";
import { Result } from "antd";
import moment from "moment";
import "moment/locale/zh-cn";
import ImageCarousel from "./ImageCarousel";
import HotelMainInfo from "./HotelMainInfo";
import RoomList from "./RoomList";
import SearchForm from "../Home/SearchForm";
import hotel from "../../data/hotel.json";

export default class Hotel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      error: null,
      hotelAndRoom: {
        hotel: {},
        rooms: ["", "", ""],
      },
    };
  }

  componentDidMount() {
    this.getDataFromServer();
  }

  getDataFromServer() {
    const url = `${process.env.REACT_APP_SERVER}`;
    const { pathname, search } = this.props.location;
    fetch(url + pathname + search)
      .then(response => response.json())
      .then(
        result => {
          this.setState({ loading: false, hotelAndRoom: result.data });
        },
        error => {
          this.setState({ loading: false, error });
        }
      );
  }

  getFakeData() {
    setTimeout(
      () =>
        this.setState({
          loading: false,
          hotelAndRoom: hotel,
        }),
      500
    );
  }

  render() {
    const { hotelAndRoom, loading, error } = this.state;
    const { history, location: locationUrl } = this.props;

    if (error)
      return <div className="error-msg">{`Error: ${error.message}`}</div>;

    if (!hotelAndRoom)
      return <Result status="404" title="404" subTitle="Êâæ‰∏çÂà∞ËØ•ÈÖíÂ∫ó" />;

    const { hotel, rooms } = hotelAndRoom;
    const imgUrls = [
      hotel.photoone,
      hotel.phototwo,
      hotel.photothree,
      hotel.photofour,
      hotel.photofive,
    ];

    const style = { maxWidth: "700px", margin: "0 auto" };

    const {
      checkInTime,
      checkOutTime,
      location,
      numOfCustomers,
    } = queryString.parse(this.props.location.search);

    const initialValues = {
      location,
      dates: [moment(checkInTime), moment(checkOutTime)],
      numOfCustomers,
    };

    return (
      <div>
        {hotel ? (
          <div>
            <ImageCarousel
              loading={loading}
              style={style}
              imgUrls={imgUrls}
            ></ImageCarousel>
            <HotelMainInfo
              loading={loading}
              style={style}
              {...hotel}
            ></HotelMainInfo>
            <div className="hoverable" style={{ ...style, marginTop: "16px" }}>
              <SearchForm
                hotelName={hotel.hoteltranslatedname}
                submitContent="Êõ¥Êñ∞"
                initialValues={initialValues}
                style={{ width: "95%" }}
                {...this.props}
              ></SearchForm>
              <RoomList
                history={history}
                location={locationUrl}
                loading={loading}
                style={style}
                rooms={rooms}
              ></RoomList>
            </div>
          </div>
        ) : (
          <Result status="404" title="404" subTitle="ÈÖíÂ∫ó‰∏çÂ≠òÂú®" />
        )}
      </div>
    );
  }
}
