import React from "react";
import { Popover, Spin } from "antd";
import "./MapPopover.css";

export default function MapPopover({ loading, onLoad, latitude, longitude }) {
  return (
    <Popover
      placement="bottom"
      trigger="hover"
      content={
        <div className="map-container">
          {loading ? <Spin className="map-spin"></Spin> : null}
          <iframe
            onLoad={onLoad}
            title="The hotel's location in Google Map"
            src={
              "https://maps.google.com/maps?q=" +
              latitude +
              "," +
              longitude +
              "&t=&z=15&ie=UTF8&iwloc=&output=embed"
            }
            width="360"
            height="270"
            frameBorder="0"
            style={{ border: 0 }}
            allowFullScreen
            aria-hidden="false"
            tabIndex="0"
            className="map"
          ></iframe>
        </div>
      }
    >
      <sub className="show-map">Êü•ÁúãÂú∞Âõæ</sub>
    </Popover>
  );
}
