import React, { Component } from "react";
import { Card, Alert } from "antd";

export default class RoomInfo extends Component {
  render() {
    const { checkInTime, checkOutTime, room, style } = this.props;
    const { capacity, count, name } = room;

    return (
      <Card
        hoverable
        style={style}
        title={
          <RoomInfoTitle
            checkInTime={checkInTime}
            checkOutTime={checkOutTime}
            roomName={name}
          ></RoomInfoTitle>
        }
      >
        <RoomInfoBody capacity={capacity} count={count}></RoomInfoBody>
      </Card>
    );
  }
}

