import React, { Component } from "react";
import queryString from "query-string";
import { Card, Skeleton } from "antd";
import AverageRating from "./AverageRating";
import HotelItemBody from "./HotelItemBody";
import HotelItemTitle from "./HotelItemTitle";
import "./HotelItem.css";

export default class HotelItem extends Component {
  onClick = () => {
    const { history, hotelid, location } = this.props;
    const {
      checkInTime,
      checkOutTime,
      location: hotelLocation,
      numOfCustomers,
    } = queryString.parse(location.search);
    if (!checkInTime || !checkOutTime) history.push(`/hotels/${hotelid}`);
    else
      history.push(
        `/hotels/${hotelid}?location=${hotelLocation}&checkInTime=${checkInTime}&checkOutTime=${checkOutTime}&numOfCustomers=${numOfCustomers}`
      );
  };

  render() {
    const { loading, averagerating } = this.props;

    return (
      <div onClick={this.onClick}>
        <Card
          className="hotel-item"
          title={
            <Skeleton active loading={loading}>
              <HotelItemTitle {...this.props}></HotelItemTitle>
            </Skeleton>
          }
          hoverable
          extra={
            <Skeleton active loading={loading}>
              <AverageRating averagerating={averagerating}></AverageRating>
            </Skeleton>
          }
        >
          <Skeleton active loading={loading}>
            <HotelItemBody {...this.props}></HotelItemBody>
          </Skeleton>
        </Card>
      </div>
    );
  }
}
