import React, { Component } from "react";
import "./App.css";
import { Switch, Route } from "react-router-dom";
import jwtDecode from "jwt-decode";
import Navbar from "./components/Navbar/Navbar";
import Banner from "./components/Home/Banner";
import HotelsList from "./components/Hotels/HotelsList";
import Hotel from "./components/Hotel/Hotel";
import Reserve from "./components/Reserve/Reserve";
import Login from "./components/Login/Login";
import Signup from "./components/Signup/Signup";
import Logout from "./components/Logout/Logout";
import Account from "./components/Account/Account";
import { getAdmin } from "./services/adminService";
import auth from "./services/authService";

class App extends Component {
  state = {};

  componentDidMount() {
    try {
      const token = auth.getToken();
      const { role, userPhone: name } = jwtDecode(token);

      if (role === "admin") {
        console.log("role is admin");
        getAdmin()
          .then(result => {
            const { hotelid: hotelId } = result.data;

            this.setState({
              user: {
                role,
                name,
                hotelId,
              },
            });
          })
          .catch(error => {
            console.log("get admin error:", error);
          });
      }

      if (role === "user") {
        this.setState({
          user: {
            role,
            name,
          },
        });
      }
    } catch (error) {}
  }

  render() {
    const { user } = this.state;

    return (
      <div>
        <Navbar user={user}></Navbar>
        <Switch>
          <Route
            path="/account"
            render={props => <Account {...props} user={user}></Account>}
          ></Route>
          <Route path="/signup" component={Signup}></Route>
          <Route path="/login" component={Login}></Route>
          <Route path="/logout" component={Logout}></Route>
          <Route path="/reserve" component={Reserve}></Route>
          <Route path="/hotels/:id" component={Hotel}></Route>
          <Route path="/hotels" component={HotelsList}></Route>
          <Route path="/" component={Banner}></Route>
        </Switch>
      </div>
    );
  }
}

export default App;
