export function updateUserInfo(user) {
  const url = `${process.env.REACT_APP_SERVER}/users/1`;
  const body = new FormData();
  for (let key in user) body.append(key, user[key]);

  return fetch(url, {
    method: "PUT",
    headers: {
      Authorization: `Bearer ${auth.getToken()}`,
    },
    body,
  });
}

export function getUploadAvatarProps() {
  return {
    name: "avatar",
    action: `${process.env.REACT_APP_SERVER}/users/1`,
    method: "PUT",
    headers: {
      Authorization: `Bearer ${auth.getToken()}`,
    },
    accept: "image/*",
    showUploadList: false,
  };
}
