import React, { Component } from "react";
import queryString from "query-string";
import { List, Button, Statistic, Skeleton } from "antd";
import { UserOutlined, HomeOutlined } from "@ant-design/icons";
import RoomIntroTagList from "./RoomIntroTagList";

export default class RoomItem extends Component {
  onReserve = (id, name, capacity, count, hotelid, e) => {
    const { history, location } = this.props;
    const { checkInTime, checkOutTime, numOfCustomers } = queryString.parse(
      location.search
    );
    history.push(
      `/reserve?checkInTime=${checkInTime}&checkOutTime=${checkOutTime}&numOfCustomers=${numOfCustomers}&id=${id}&name=${name}&capacity=${capacity}&count=${count}&hotelId=${hotelid}`
    );
  };

  render() {
    const {
      id,
      price,
      capacity,
      count,
      name,
      intro,
      loading,
      hotelid,
    } = this.props;

    return (
      <div>
        <List.Item
          actions={[
            <Button
              onClick={e =>
                this.onReserve(id, name, capacity, count, hotelid, e)
              }
              type="primary"
            >
              {price}Ôø• È¢ÑÂÆö
            </Button>,
          ]}
          extra={
            <div>
              <Statistic
                title="‰∫∫Êï∞ÈôêÂà∂"
                value={capacity}
                prefix={<UserOutlined />}
              ></Statistic>
              <Statistic
                title="Ââ©‰ΩôÊàøÈó¥"
                value={count}
                prefix={<HomeOutlined />}
              ></Statistic>
            </div>
          }
        >
          <Skeleton active loading={loading}>
            <List.Item.Meta
              title={name}
              description={<RoomIntroTagList intro={intro}></RoomIntroTagList>}
            ></List.Item.Meta>
          </Skeleton>
        </List.Item>
      </div>
    );
  }
}
