import React, { Component } from "react";
import moment from "moment";
import "moment/locale/zh-cn";
import {
  Layout,
  Menu,
  Form,
  Input,
  Rate,
  InputNumber,
  TimePicker,
  DatePicker,
  Row,
  Col,
  Button,
  Select,
} from "antd";
import { getHotel } from "../../services/hotelService";
import { getChains } from "../../services/chainService";
import { getBrands } from "../../services/brandService";
import { updateHotel } from "../../services/hotelService";
import "./Admin.css";

const { Content, Sider } = Layout;
const { Item } = Form;
const { Option } = Select;

export default class Admin extends Component {
  render() {
    const { hotelId } = this.props;

    return (
      <Layout className="site-layout-background admin-layout">
        <Sider className="site-layout-background" width={200}>
          <Menu mode="inline" defaultSelectedKeys={["hotel"]}>
            <Menu.Item key="hotel">ÈÖíÂ∫ó</Menu.Item>
            <Menu.Item key="rooms">ÊàøÈó¥</Menu.Item>
            <Menu.Item key="reservations">ËÆ¢Âçï</Menu.Item>
          </Menu>
        </Sider>
        <Content style={{ padding: "0 24px", height: "calc(100vh - 10em)" }}>
          <HotelForm hotelId={hotelId}></HotelForm>
        </Content>
      </Layout>
    );
  }
}

class HotelForm extends Component {
  formRef = React.createRef();

  state = {
    updating: false,
  };

  componentDidMount() {
    const { hotelId } = this.props;

    getBrands().then(brands => {
      this.setState({ brands });
    });

    getChains().then(chains => {
      this.setState({ chains });
    });

    getHotel(hotelId).then(hotel => {
      this.setState({ hotel });
    });
  }

  handleFinish = values => {
    this.setState({ updating: true });

    const { checkin, checkout, yearopened, yearrenovated } = values;

    const toSubmit = {
      ...this.state.hotel,
      ...values,
      checkin: checkin.format("h:mm:ss A"),
      checkout: checkout.format("h:mm:ss A"),
      yearopened: yearopened.format("yyyy"),
      yearrenovated: yearrenovated.format("yyyy"),
    };

    delete toSubmit.brandname;
    delete toSubmit.chainname;

    updateHotel()
      .then(result => {
        result.json();
      })
      .then(({ data }) => {
        this.setState({ updating: false });
        this.formRef.current.setFieldsValue(this.hotel2FormValues(data));
      })
      .catch(error => {
        console.log(error);
        this.setState({ updating: false });
      });
  };

  hotel2FormValues(hotel) {
    return {
      ...hotel,
      checkin: moment(hotel.checkin, "h:mm:ss A"),
      checkout: moment(hotel.checkout, "h:mm:ss A"),
      yearopened: moment(hotel.yearopened, "yyyy"),
      yearrenovated: moment(hotel.yearrenovated, "yyyy"),
    };
  }

  render() {
    if (!this.state.hotel) return null;

    const { hotel, chains, brands, updating } = this.state;

    return (
      <Form
        ref={this.formRef}
        initialValues={{
          ...hotel,
          chainid: chains.find(c => c.chainname === hotel.chainname).chainid,
          brandid: brands.find(b => b.brandname === hotel.brandname).brandid,
          checkin: moment(hotel.checkin, "h:mm:ss A"),
          checkout: moment(hotel.checkout, "h:mm:ss A"),
          yearopened: moment(hotel.yearopened, "yyyy"),
          yearrenovated: moment(hotel.yearrenovated, "yyyy"),
        }}
        onFinish={this.handleFinish}
        layout="vertical"
      >
        <Item label="id" name="hotelid">
          <Input disabled></Input>
        </Item>
        <Item label="ËøûÈîÅ" name="chainid">
          <Select>
            {chains.map(c => (
              <Option key={c.chainid} value={c.chainid}>
                {c.chainname}
              </Option>
            ))}
          </Select>
        </Item>
        <Item label="ÂìÅÁâå" name="brandid">
          <Select>
            {brands.map(c => (
              <Option key={c.brandid} value={c.brandid}>
                {c.brandname || "No brand"}
              </Option>
            ))}
          </Select>
        </Item>
        <Item label="Ëã±ÊñáÂêç" name="hotelname">
          <Input></Input>
        </Item>
        <Item label="Ê≠£ÂºèÂêç" name="hotelformerlyname">
          <Input></Input>
        </Item>
        <Item label="‰∏≠ËØëÂêç" name="hoteltranslatedname">
          <Input></Input>
        </Item>
        <Item label="Âú∞ÂùÄ" name="address">
          <Input></Input>
        </Item>
        <Item label="ÈÇÆÁºñ" name="zipcode">
          <Input></Input>
        </Item>
        <Item label="ÊòüÁ∫ß" name="starrating">
          <Rate allowHalf></Rate>
        </Item>
        <TwoColInput
          input1={
            <Item label="ÁªèÂ∫¶" name="longitude">
              <InputNumber
                style={{ minWidth: "90%" }}
                min={-180}
                max={180}
                step={0.000001}
              ></InputNumber>
            </Item>
          }
          input2={
            <Item label="Á∫¨Â∫¶" name="latitude">
              <InputNumber
                style={{ minWidth: "90%" }}
                min={-180}
                max={180}
                step={0.000001}
              ></InputNumber>
            </Item>
          }
        ></TwoColInput>
        <TwoColInput
          input1={
            <Item label="ÂÖ•‰ΩèÊó∂Èó¥" name="checkin">
              <TimePicker
                style={{ minWidth: "90%" }}
                use12Hours
                format="h:mm:ss A"
              ></TimePicker>
            </Item>
          }
          input2={
            <Item label="ÈÄÄÊàøÊó∂Èó¥" name="checkout">
              <TimePicker
                style={{ minWidth: "90%" }}
                use12Hours
                format="h:mm:ss A"
              ></TimePicker>
            </Item>
          }
        ></TwoColInput>
        <Item label="ÊàøÈó¥Êï∞" name="numberrooms">
          <InputNumber></InputNumber>
        </Item>
        <Item label="Ê•ºÂ±ÇÊï∞" name="numberfloors">
          <InputNumber></InputNumber>
        </Item>
        <TwoColInput
          input1={
            <Item label="ÂºÄ‰∏öÂπ¥‰ªΩ" name="yearopened">
              <DatePicker
                style={{ minWidth: "90%" }}
                picker="year"
              ></DatePicker>
            </Item>
          }
          input2={
            <Item label="ÁøªÊñ∞Âπ¥‰ªΩ" name="yearrenovated">
              <DatePicker
                style={{ minWidth: "90%" }}
                picker="year"
              ></DatePicker>
            </Item>
          }
        ></TwoColInput>
        <Item label="‰ªãÁªç" name="overview">
          <Input.TextArea autoSize></Input.TextArea>
        </Item>
        <Item label="ÊúÄ‰Ωé‰ª∑" name="minprice">
          <InputNumber></InputNumber>
        </Item>
        <Item>
          <Button loading={updating} type="primary" htmlType="submit">
            ‰øÆÊîπ
          </Button>
        </Item>
      </Form>
    );
  }
}

function TwoColInput({ input1, input2 }) {
  return (
    <Row>
      <Col span="12">{input1}</Col>
      <Col span="12">{input2}</Col>
    </Row>
  );
}
