import React, { Component } from "react";
import HotelItem from "./HotelItem";
import hotels from "../../data/hotels.json";
import "./HotelsList.css";

export default class HotelsList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      loading: true,
      hotels: ["", "", ""],
    };
  }

  componentDidMount() {
    this.getDataFromServer();
  }

  getDataFromServer() {
    const url = `${process.env.REACT_APP_SERVER}/hotels`;
    const queryString = this.props.location.search;
    fetch(url + queryString)
      .then(response => response.json())
      .then(
        result => {
          if (!result.data)
            this.setState({ loading: false, error: { message: result.msg } });
          else this.setState({ loading: false, hotels: result.data });
        },
        error => {
          this.setState({ loading: false, error });
        }
      )
      .catch(error => {
        this.setState({ loading: false, error });
      });
  }

  getFakeData() {
    setTimeout(() => {
      this.setState({
        loading: false,
        hotels: hotels,
      });
    }, 500);
  }

  render() {
    const { loading, hotels, error } = this.state;

    return (
      <div className="hotel-list">
        {error ? (
          <div className="error-msg">{`Error: ${error.message}`}</div>
        ) : (
          hotels.map((hotel, index) => (
            <HotelItem
              {...this.props}
              key={
                hotel.hotelid === null || hotel.hotelid === undefined
                  ? index
                  : hotel.hotelid
              }
              loading={loading}
              {...hotel}
            ></HotelItem>
          ))
        )}
      </div>
    );
  }
}
