import hotelAndRooms from "../data/hotel.json";
import auth from "./authService";

export function getHotel(id) {
  return new Promise((resolve, reject) => {
    resolve(hotelAndRooms.hotel);
  });
}

export function adminGetHotel() {
  // return
}

export function updateHotel(id, hotel) {
  const url = `${process.env.REACT_APP_SERVER}/hotels/${id}`;
  const body = new FormData();
  for (let key in hotel) body.append(key, hotel[key]);

  return fetch(url, {
    method: "PUT",
    headers: {
      Authorization: `Bearer ${auth.getToken()}`,
    },
    body,
  });
}
