import React, { Component } from "react";
import { UserOutlined } from "@ant-design/icons";
import {
  Avatar,
  Upload,
  Button,
  Space,
  Input,
  Form,
  Radio,
  Card,
  message,
  Skeleton,
} from "antd";
import {
  getUser,
  getUploadAvatarProps,
  updateUserInfo,
} from "../../services/userService";
import { getPayWays } from "../../services/payWayService";

export default class UserSetting extends Component {
  state = {
    loading: true,
    uploading: false,
    updating: false,
  };

  componentDidMount() {
    getUser(1)
      .then(result => {
        this.setState({ user: result.data, loading: false });
      })
      .catch(error => {
        message.error(error.message);
        this.setState({ loading: false });
      });
  }

  handleFinish = values => {
    this.setState({ updating: true });
    updateUserInfo(values)
      .then(() => {
        this.setState({ updating: false });
      })
      .catch(error => {
        message.error(error.message);
        this.setState({ updating: false });
      });
  };

  handleUploadAvatarChange = info => {
    this.setState({ uploading: true });

    if (info.file.status === "error") {
      message.error(`ÂõæÁâá ${info.file.name} ‰∏ä‰º†Â§±Ë¥•ÔºåËØ∑ÈáçËØï`);
      this.setState({ uploading: false });
    }
    if (info.file.status === "done") {
      message.success(`ÂõæÁâá ${info.file.name} ‰∏ä‰º†ÊàêÂäü`);
      const { user } = this.state;
      this.setState({
        user: {
          ...user,
          avatar: JSON.parse(info.file.response).data.user.avatar,
        },
        uploading: false,
      });
    }
  };

  render() {
    const { user, loading, uploading, updating } = this.state;

    return (
      <Skeleton loading={loading}>
        {user ? (
          <>
            <AvatarSection
              uploading={uploading}
              src={user.avatar}
              style={{ marginBottom: "2em" }}
              onUploadAvatarChange={this.handleUploadAvatarChange}
            ></AvatarSection>
            <UserInfoSection
              user={user}
              onFinish={this.handleFinish}
              updating={updating}
            ></UserInfoSection>
          </>
        ) : null}
      </Skeleton>
    );
  }
}

class AvatarSection extends Component {
  render() {
    const { src, style, onUploadAvatarChange, uploading } = this.props;

    return (
      <div style={style}>
        <Space>
          <Avatar
            src={src}
            size={128}
            icon={<UserOutlined></UserOutlined>}
          ></Avatar>
          <Upload {...getUploadAvatarProps()} onChange={onUploadAvatarChange}>
            <Button loading={uploading}>‰∏ä‰º†Â§¥ÂÉè</Button>
          </Upload>
        </Space>
      </div>
    );
  }
}

