import React, { Component } from "react";
import Admin from "./Admin";
import "./Account.css";
import User from "./User";

export default class Account extends Component {
  render() {
    const { user } = this.props;
    if (!user) return null;
    return user.role === "admin" ? (
      <Admin hotelId={user.hotelId}></Admin>
    ) : (
      <User></User>
    );
  }
}
